.userInfo {
    margin-bottom: 20px;
    display: flex;
    align-items: center;
    background-color: #fff;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 4px 4px 8px 0px rgba(16, 29, 39, 0.2);
}
.avaBody {
    margin-left:40px;
    width: 80px;
    height: 80px;
    background-color: #dbddbbbb;
    border-radius: 50%;
    overflow: hidden;
    margin-right: 20px;
    border: solid 2px #054d91;
    box-shadow: 4px 4px 8px 0px rgba(16, 29, 39, 0.2);
}

.avatar {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.descr {
    color: #054d91;
    margin-left: 40px;
}

.descr h2 {
    font-size: 24px;
    font-weight: 600;
    margin-bottom: 10px;
}

.descr p {
    font-size: 20px;
    font-weight: 600;
    margin-bottom: 5px;
    line-height: 1.5;
}

.listTitle{
    font-size: 20px;
    font-weight: 600;
}